




























































































import { Vue, Component, Watch } from "vue-property-decorator";
import logger from "aiwen-logger";
import compareVersions from "compare-versions";

import {
  getSmsCode,
  generateCodeUrl,
  phoneLoginApi,
  weiboLoginApi,
  weixinLoginApi,
  getUserInfo,
} from "@/api/user";

import {
  UserTypeEnum,
  LoginSourceEnum,
  DeviceEnum,
  WeiboClientTypeEnum,
  WeixinClientTypeEnum,
} from "@/enums/businessEnum";

import { isValidMobileS } from "@/utils/validateUtil";
import { setDomainToken } from "@/utils/auth";

import { Toast } from "@/hooks/web/useToast";

let smsTimer: any = {};

@Component({
  name: "UserLogin",
})
export default class UserLogin extends Vue {
  private checkedPolicy = false; // 是否勾选协议
  private isShowAd = false; //TODO 临时屏蔽 是否展示 广告位
  private isShowPhoneLogin = false; // 是否显示手机号登录
  private device: string = window.$device; // 设备
  private platform: string = window.$platform; // 平台
  private weiboResult: any = {}; // 微博授权返回的信息
  private weixinResult: any = {}; // 微信授权返回的信息
  private loginForm = {
    // 登录表单
    phone: "",
    sms: "",
  };
  private formState = {
    // 表单状态
    loading: false,
    disabled: true,
  };
  // 验证码 相关属性
  private sms = {
    text: "获取验证码",
    countTime: 60,
    disabled: false,
    pending: false,
  };
  // css 变量控制
  private styleVar = {
    "--space": "60px",
    "--logo": "40px",
  };
  // 微博、微信授权回调 code
  get code() {
    return (this as any).$route.query.code || "";
  }
  // 登录流程走完回调 地址
  get refer() {
    return (this as any).$route.query.refer || "";
  }
  // 触发模块 埋点
  get triggerModule() {
    return (this as any).$route.query.triggerModule || "";
  }
  //  触发动作 埋点
  get triggerAction() {
    return (this as any).$route.query.triggerAction || "";
  }
  //  判断是否是ios14以上的版本
  get isIOS14() {
    // iphone os 14
    const str = navigator.userAgent.toLowerCase();
    const versionList: any = str.match(/cpu iphone os (.*?) like mac os/);
    const version = versionList && versionList[1].replace(/_/g, ".");
    if (version && compareVersions.compare(version, "14.0", ">=")) {
      return true;
    } else {
      return false;
    }
  }
  getPlatformTrans() {
    let platformTrans = "其他";
    if (this.platform === "weixin") platformTrans = "微信";
    if (this.platform === "weibo") platformTrans = "微博";
    return platformTrans;
  }

  @Watch("loginForm", { deep: true })
  private onInputChange() {
    // 监听手机号，验证码输入
    if (isValidMobileS(this.loginForm.phone) && this.loginForm.sms.length === 6) {
      this.formState.disabled = false;
    } else {
      this.formState.disabled = true;
    }
  }
  public created() {
    // 第三方授权登录判断
    this.thirdAuth();
  }
  public mounted() {
    // 登录页面-浏览-埋点
    logger.emit("c_login_page_view", {
      login_type: "H5",
      H5_surroundings: this.getPlatformTrans,
      trigger_module: this.triggerModule,
      trigger_action: this.triggerAction,
    });
  }
  // 验证是否在 微信，微博环境下进行登录调用
  private thirdAuth() {
    if (this.platform === "weixin") {
      if (this.code) {
        // 回调页面获取 code，进行微信授权登录 后台调接口
        this.handleWxLogin();
      } else {
        this.generateCode(undefined, "account_small_aiwen");
      }
    } else if (this.platform === "weibo") {
      // 判断 是否是ios14.0的版本，是的话直接走手机号登录
      if (this.isIOS14) {
        this.isShowPhoneLogin = true;
        return;
      }
      if (this.code) {
        // 回调页面获取 code，进行微博授权登录 后台调接口
        this.handleWeiboLogin();
      } else {
        this.generateCode("aiwen_doctor", undefined);
      }
    } else {
      this.isShowPhoneLogin = true;
    }
  }
  // 勾选 用户协议 事件
  public handleCheckPolicy() {
    this.checkedPolicy = !this.checkedPolicy;
  }
  // input focus 事件
  public handleFocus() {
    this.styleVar["--space"] = "30px";
    this.styleVar["--logo"] = "10px";
  }
  // input blur 事件
  public handleBlur() {
    setTimeout(() => {
      this.styleVar["--space"] = "60px";
      this.styleVar["--logo"] = "40px";
    });
  }
  // 微信 授权登录
  public handleWxLogin() {
    const params: any = {
      anonymousId: logger.getAnonymousId() || "",
      clientCode: WeixinClientTypeEnum.ACCOUNT,
      source: DeviceEnum.H5,
      code: this.code,
      loginSource: LoginSourceEnum.PATIENT,
      // TODO：兼容JSP页面的 login_token
      addCookie: true,
    };
    weixinLoginApi(params)
      .then((res) => {
        if (res && res.token) {
          // 微信登录埋点
          logger.emit("c_login_page_click", {
            login_type: "H5",
            H5_surroundings: this.getPlatformTrans,
            trigger_module: this.triggerModule,
            trigger_action: this.triggerAction,
            button_content: "微信登录",
          });
          // 有返回 token 证明已经绑定手机号，进行登录后的回调
          this.callbackLogin(res);
        } else {
          // 展示手机号登录页面
          // 获取微信返回的数据信息
          this.weixinResult = res;
          this.isShowPhoneLogin = true;
        }
      })
      .catch(() => {
        this.isShowPhoneLogin = true;
      });
  }
  // 微博 授权登录
  public handleWeiboLogin() {
    const params: any = {
      // TODO 临时用 医端的 code
      clientCode: WeiboClientTypeEnum.DOCTOR,
      source: DeviceEnum.H5,
      code: this.code,
      loginSource: LoginSourceEnum.PATIENT,
      redirectUri: process.env.VUE_APP_CustomLoginUrl,
      anonymousId: logger.getAnonymousId() || "",
      // TODO：兼容JSP页面的 login_token
      addCookie: true,
    };
    weiboLoginApi(params)
      .then((res) => {
        // console.log("handleWeiboLogin", res);
        if (res && res.token) {
          // 微博登录埋点
          logger.emit("c_login_page_click", {
            login_type: "H5",
            H5_surroundings: this.getPlatformTrans,
            trigger_module: this.triggerModule,
            trigger_action: this.triggerAction,
            button_content: "微博登录",
          });
          // 有返回 token 证明已经绑定手机号，进行登录后的回调
          this.callbackLogin(res);
        } else {
          // 展示手机号登录页面
          // 获取微博返回的数据信息
          this.weiboResult = res;
          this.isShowPhoneLogin = true;
        }
      })
      .catch(() => {
        this.isShowPhoneLogin = true;
      });
  }
  public async callbackLogin(res: any) {
    setDomainToken(res.token); // 设置 .health.sina.cn 的 login_token
    localStorage.setItem("refreshToken", res.refreshToken);
    localStorage.setItem("token", res.token);
    try {
      const userinfo = await getUserInfo();
      const userinfoStr = JSON.stringify(userinfo) || "";
      sessionStorage.setItem("aiwen_user_userinfo", userinfoStr);
    } catch (error) {
      console.log("---获取用户信息失败：" + error);
    }
    logger.login(res.userId); // 神策埋点用户关联，把登陆前的匿名id和登录后的用户id关联起来。
    // hack 兼容处理linkcard，ios14微博环境，iframe限制获取cookie的问题
    const isSelfPage = window.self === window.top; // 判断是否在iframe内层
    // && this.platform === "weibo"
    if (this.isIOS14 && !isSelfPage) {
      // window.top.location.replace 跳出外层-进入一个中转的loading页
      console.log("ios14-weibo-iframe 环境");
      let refer = "";
      if (this.refer) {
        if (this.refer.startsWith("http")) {
          refer = decodeURIComponent(this.refer);
        } else {
          refer = `${process.env.VUE_APP_UserH5Url}${this.refer}`;
        }
      } else {
        if (document.referrer) {
          refer = document.referrer;
        } else {
          refer = `${process.env.VUE_APP_UserH5Url}/home`;
        }
      }
      window.top.location.replace(`./loading?refer=${refer}`);
    }
    if (this.refer) {
      if (this.refer.startsWith("http")) {
        //! 注：不同源的地址，浏览器存储不互通
        window.location.replace(decodeURIComponent(this.refer));
      } else {
        window.location.replace(`${process.env.VUE_APP_UserH5Url}${this.refer}`);
      }
    } else {
      if (document.referrer) {
        // refer 不存在
        window.location.replace(document.referrer);
      } else {
        window.location.replace(`${process.env.VUE_APP_UserH5Url}/home`);
      }
    }
  }
  // 获取生成第三方登录的 code 的链接
  public generateCode(weiBoClientCode: any, weiXinClientCode: any) {
    let thirdRefer = this.refer || document.referrer;
    const encodeUri = encodeURIComponent(thirdRefer);
    const loginUrl = process.env.VUE_APP_CustomLoginUrl;
    const params = {
      redirectUri: `${loginUrl}?triggerModule=${this.triggerModule}&triggerAction=${this.triggerAction}&refer=${encodeUri}`,
      weiBoClientCode,
      weiXinClientCode,
      forceLogin: false,
    };
    generateCodeUrl(params)
      .then((res) => {
        // 跳转到鉴权接口
        window.location.href = res;
      })
      .catch(() => {
        this.isShowPhoneLogin = true;
      });
  }
  public goback() {
    this.$router.go(-1);
  }
  // 获取验证码
  public async handleGetSmsCode() {
    if (this.sms.disabled || this.sms.pending) {
      return false;
    }
    if (!this.loginForm.phone) {
      Toast("请输入手机号");
      return false;
    }
    if (!isValidMobileS(this.loginForm.phone)) {
      Toast("请输入正确的手机号");
      return false;
    }
    this.loginForm.sms = "";
    this.sms.countTime = 60;
    this.sms.pending = true;
    try {
      const params = {
        phone: this.loginForm.phone,
        userCode: UserTypeEnum.USER,
      };
      await getSmsCode(params);
      this.sms.pending = false;
      this.sms.disabled = true;
      clearTimeout(smsTimer);
      smsTimer = setInterval(() => {
        this.sms.countTime--;
        if (this.sms.countTime === 0) {
          this.sms.disabled = false;
          this.sms.text = "重新获取";
          clearTimeout(smsTimer);
        }
      }, 1000);
    } catch (err) {
      this.sms.disabled = false;
      this.sms.pending = false;
      Toast("验证码发送失败，请重试");
    }
  }
  // 登录操作
  public async handleLogin() {
    if (this.formState.loading) return false;
    if (!this.loginForm.phone) {
      Toast("请输入手机号");
      return false;
    }
    if (!isValidMobileS(this.loginForm.phone)) {
      Toast("请输入正确的手机号");
      return false;
    }
    if (this.loginForm.sms.length !== 6) {
      Toast("请输入正确的验证码");
      return false;
    }
    if (!this.checkedPolicy) {
      Toast("请勾选用户协议");
      return false;
    }
    // 手机号登录埋点
    logger.emit("c_login_page_click", {
      login_type: "H5",
      H5_surroundings: this.getPlatformTrans,
      trigger_module: this.triggerModule,
      trigger_action: this.triggerAction,
      button_content: "手机号登录",
    });
    try {
      this.formState.loading = true;
      const params: any = {
        phone: this.loginForm.phone,
        checkNum: this.loginForm.sms,
        loginSource: LoginSourceEnum.PATIENT,
        source: DeviceEnum.H5,
        anonymousId: logger.getAnonymousId() || "",
        // TODO：兼容JSP页面的 login_token
        addCookie: true,
      };
      if (this.platform === "weibo" && this.weiboResult.weiboUid) {
        params.weiBoRegistered = this.weiboResult.registered;
        params.weiboUid = this.weiboResult.weiboUid;
        params.weiBoClientCode = "aiwen_doctor";
      }
      if (this.platform === "weixin" && this.weixinResult.unionId) {
        params.weiXinRegistered = this.weixinResult.registered;
        params.weiXinClientCode = "account_small_aiwen";
        params.unionId = this.weixinResult.unionId;
      }
      const res = await phoneLoginApi(params);
      this.formState.loading = false;
      Toast("登录成功");
      this.callbackLogin(res);
    } catch (err) {
      this.formState.loading = false;
    }
  }
  // 跳转下载APP页，或者唤醒APP
  public handleDownloadApp() {
    const downloadUrl = "https://health.sina.cn/zt/app/down/";
    window.location.href = downloadUrl;
  }
  // 关闭广告栏
  public handleCloseAd() {
    this.isShowAd = false;
  }
}
