import Cookies from "js-cookie";

const TokenKey = "login_token";

export function getToken() {
  return Cookies.get(TokenKey);
}

export function getLocalToken() {
  return localStorage.getItem("token");
}

export function setToken(token: string) {
  return Cookies.set(TokenKey, token);
}

export function setDomainToken(token: string) {
  return Cookies.set(TokenKey, token, { domain: ".health.sina.cn" });
}

export function removeToken() {
  return Cookies.remove(TokenKey);
}

export function removeDomainToken() {
  return Cookies.remove(TokenKey, { domain: ".health.sina.cn" });
}
